          data-label="No">
          {{ String(monthlyFigureDetailObjectOut.monthlyFigureTypeDetailObjectOut.monthlyFigureTypeDetailOut.figurePrice)
          .replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
