          .replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
        <td v-else data-label="No">0</td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">컨버지</th>
        <td data-label="No">
          {{ monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyConvergeFigureGradeDetailOut.figureCount }}
        </td>
        <td
          v-if="monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyConvergeFigureGradeDetailOut.figurePrice !== 0"
          data-label="No">
          {{ String(monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyConvergeFigureGradeDetailOut.figurePrice)
          .replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
        <td v-else data-label="No">0</td>
      </tr>

      <tr class="hover-tr">
        <th scope="col">EG</th>
        <td data-label="No">
          {{ monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyEgFigureGradeDetailOut.figureCount }}
        </td>
        <td
          v-if="monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyEgFigureGradeDetailOut.figurePrice !== 0"
          data-label="No">
          {{ String(monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyEgFigureGradeDetailOut.figurePrice)
          .replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
        <td v-else data-label="No">0</td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">HG</th>
        <td data-label="No">
          {{ monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyHgFigureGradeDetailOut.figureCount }}
        </td>
        <td
          v-if="monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyHgFigureGradeDetailOut.figurePrice !== 0"
          data-label="No">
          {{ String(monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyHgFigureGradeDetailOut.figurePrice)
          .replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
        <td v-else data-label="No">0</td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">RG</th>
        <td data-label="No">
          {{ monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyRgFigureGradeDetailOut.figureCount }}
        </td>
        <td
          v-if="monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyRgFigureGradeDetailOut.figurePrice !== 0"
          data-label="No">
          {{ String(monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyRgFigureGradeDetailOut.figurePrice)
          .replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
        <td v-else data-label="No">0</td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">MG</th>
        <td data-label="No">
          {{ monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyMgFigureGradeDetailOut.figureCount }}
        </td>
        <td
          v-if="monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyMgFigureGradeDetailOut.figurePrice !== 0"
          data-label="No">
          {{ String(monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyMgFigureGradeDetailOut.figurePrice)
          .replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
        <td v-else data-label="No">0</td>
      </tr>
      <tr class="hover-tr">
        <th scope="col">PG</th>
        <td data-label="No">
          {{ monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyPgFigureGradeDetailOut.figureCount }}
        </td>
        <td
          v-if="monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyPgFigureGradeDetailOut.figurePrice !== 0"
          data-label="No">
          {{ String(monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyPgFigureGradeDetailOut.figurePrice)
          .replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
