      <tr class="hover-tr">
        <th scope="col">기타</th>
        <td data-label="No">
          {{ monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyEtcFigureGradeDetailOut.figureCount }}
        </td>
        <td
          v-if="monthlyFigureDetailObjectOut.monthlyFigureGradeDetailObjectOut.monthlyEtcFigureGradeDetailOut.figurePrice !== 0"
          data-label="No">
