      </tr>
      </tbody>
    </table>
    <br />
    <table class="responsive">
      <caption>
        <span>
          * 상세 구매 목록
        </span>
      </caption>
      <thead>
      <tr>
        <th scope="col">등급</th>
        <th scope="col">이름</th>
        <th scope="col">구매날짜</th>
        <th scope="col">가격</th>
      </tr>
      </thead>
      <tbody>
      <tr
        v-for="(item, index) in selected.figureDetail" :key="index"
        class="hover-tr"
      >
        <td data-label="등급">{{ item.grade.value }}</td>
        <td data-label="이름">{{ item.figureName }}</td>
        <td
          v-if="item.date !== null"
          data-label="구매날짜">
          {{ String(item.date).split('T')[0] }}
        </td>
        <td v-else data-label="구매날짜">
          등록되지 않음
        </td>
        <td data-label="가격">
          {{ String(item.price).replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,') }}
        </td>
      </tr>
      </tbody>
    </table>
    <div class="input-wrapper" style="margin-top: 10px;">
      <button
        @click="_routeGoBack"
        class="cancel__button">
        뒤로가기
      </button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'pinia'
import { figureStore } from '@/store/figures'
import { usersStore } from '@/store/users'

export default {
  name: 'user-gunpla-monthly-detail',
  data () {
    return {
      selected: {
        year: 0,
        month: 0,
        figureDetail: [
          {
            figureOid: 0,
            figureName: null,
            type: { code: '', value: '' },
            wishYn: false,
            nickName: null,
            version: null,
            series: null,
            grade: { code: '', value: '' },
            generation: null,
            price: 0,
            date: null,
            store: null,
            startDate: null,
            endDate: null,
            completeYn: { code: '', value: '' }
          }
        ]
      },
      monthlyFigureDetailObjectOut: {
        monthlyFigureTypeDetailObjectOut: {
          monthlyFigureTypeDetailOut: {
            figureType: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyNoneFigureDetailOut: {
            figureType: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          }
        },
        monthlyFigureGradeDetailObjectOut: {
          monthlyConvergeFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlySdFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyEgFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyHgFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyRgFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyMgFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyPgFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          },
          monthlyEtcFigureGradeDetailOut: {
            figureGrade: { code: '', value: '' },
            figureCount: 0,
            figurePrice: 0
          }
        }
      }
    }
  },
  computed: {
    ...mapState(usersStore, ['userCustomInfo']),
    ...mapState(figureStore, ['getFigureMonthlyParams'])
  },
  methods: {
    _routeGoBack () {
      this.$router.go(-1)
    },
    _getMonthlyFigureInfoDetail (userOid, year, month) {
      this.getMonthlyFigureInfoDetail(
        userOid,
        year,
        month
      ).then(res => {
        this.monthlyFigureDetailObjectOut = res
      })
    }
  },
  async created () {
    if (this.$route.params.monthlyParam !== undefined) {
      this.selected.year = JSON.parse(this.$route.params.monthlyParam).year
      this.selected.month = JSON.parse(this.$route.params.monthlyParam).month
      this.selected.figureDetail = JSON.parse(this.$route.params.monthlyParam).figureDetail
      await this._getMonthlyFigureInfoDetail(
        this.userCustomInfo.userOid,
        this.selected.year,
        this.selected.month
      )
    }
  },
  async mounted () {
    if (this.selected.year === 0) {
      this.selected.year = this.getFigureMonthlyParams.year
      this.selected.month = this.getFigureMonthlyParams.month
      await this._getMonthlyFigureInfoDetail(
        this.userCustomInfo.userOid,
        this.selected.year,
        this.selected.month
      )
    }
  }
}
</script>
<style scoped lang="scss">
.grid-item {
  height: 160px !important;
}
.button__wrapper {
  margin-top: 30px;
  text-align: right;
}
</style>
